<template>
  <div class="hot cfx">
    <div class="hot-left fl">
      <img class="fl" src="data:image/png;base64," alt="">
      <span class="hl-title fl">热点~</span>
    </div>
    <div class="hot-right fl">
      <swiper :options="swiperOption">
        <!-- <div class="swiper-pagination swiper-pagination-h" slot="pagination"></div> -->
        <swiper-slide v-for="item in newsLists" :key="item.id">
            <p class="hr-title">{{ item.title }}</p>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>
<script>
  export default{
    data () {
      return {
        newsLists: [
          {
            'title': '世卫组织当选总干事重申将继续坚持一个中国原则',
            'url': ''
          },
          {
            'title': '国务院7个部门因“红顶中介”被通报 含环保部',
            'url': ''
          },
          {
            'title': '港媒称港珠澳大桥香港段水泥掺假始于2015年',
            'url': ''
          },
          {
            'title': '衡水中学在浙江开分校引争议 法人代表拒绝接受采访',
            'url': ''
          }
        ],
        swiperOption: {
          pagination: '',
          paginationClickable: true,
          spaceBetween: 0,
          direction: 'vertical',
          autoplay: 2500,
          loop: true,
          autoplayDisableOnInteraction: false
        }
      }
    }
  }
</script>
<style scoped>
  .hot{width: 100%;height: 30px;overflow: hidden;font-size: 12px;}
  .hot-left{width: 20%;margin-left: 2%;}
  .hot-left img{width: 30%;}
  .hot-left span{height: 30px;line-height: 30px;}
  .hot-right{width: 70%;height: 100%;overflow: hidden;text-align: left;}
  .hr-title{height: 30px;line-height: 30px;}
</style>



